import CONFIG from '../../globals/config';

class RestoItem extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');

    this._resto = {
      id: 'SAMPLE_ID',
      name: 'SAMPLE_NAME',
      description: 'SAMPLE_DESCRIPTION',
      pictureId: 'SAMPLE_PICTUREID',
      city: 'SAMPLE_CITY',
      rating: 'SAMPLE_RATING',
    };
  }

  setResto(value) {
    this._resto.id = value.id;
    this._resto.name = value.name;
    this._resto.description = value.description;
    this._resto.pictureId = value.pictureId;
    this._resto.city = value.city;
    this._resto.rating = value.rating;
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      .resto_item {
        border-radius: 1rem;
        box-shadow: 0 0.1em 0.2em var(--primaryColor), 0 0 0.2em var(--primaryColor) inset;

        .resto_thumbnail {
          position: relative;
          img {
            width: 100%;
            height: 15em;
            object-fit: cover;
            border-radius: 1rem 1rem 0 0;
            box-shadow: 0.1em 0.1em 0.3em var(--black), -0.1em -0.1em 0.3em var(--black);
          }

          .resto_city-rating {
            color: var(--white);
            padding: 0.4em 1.5em;
            border-radius: 0 0.5rem 0 0;
            background-color: var(--primaryColor);
            position: absolute;
            bottom: 0.18em;
            left: 0.03em;

            display: flex;
            min-width: 40%;
            flex-direction: row;
            gap: 1em;
            justify-items: center;
            justify-content: space-evenly;
            box-shadow: -0.1em -0.1em 0.1em var(--primaryColor);
          }
        }

        .resto_info {
          padding: 1em;
          margin: 0 auto;

          .resto_name {
            color: var(--primaryColor);
            a {
              text-decoration: none;
              padding: 0.3em 0;
              color: inherit;
            }
          }

          .resto_desc {
            color: var(--secondColor);
            line-height: 1.5rem;
            margin: 1em 0;
            padding: 0.3em;
            max-height: 8em;
            overflow: hidden;
            overflow-wrap: break-word;

            text-overflow: clip;
            display: -webkit-box;
            -webkit-line-clamp: 4;
            -webkit-box-orient: vertical;
            line-clamp: 4;
          }
        }
      }
    `;
  }

  render() {
    this.updateStyle();
    this.innerHTML = `
      ${this._style.outerHTML}
        <article class="resto_item">
          <div class="resto_thumbnail">
            <picture>
              <source media="(max-width: 600px)" srcset="${CONFIG.BASE_IMAGE_URL_SMALL}${this._resto.pictureId}" data-srcset="${CONFIG.BASE_IMAGE_URL_SMALL}${this._resto.pictureId}">
              <source media="(max-width: 1000px)" srcset="${CONFIG.BASE_IMAGE_URL_MEDIUM}${this._resto.pictureId}" data-srcset="${CONFIG.BASE_IMAGE_URL_MEDIUM}${this._resto.pictureId}">
              <img tabindex="0" aria-label="Nama restoran ${this._resto.name}" src="${CONFIG.BASE_IMAGE_URL_LARGE}${this._resto.pictureId}" alt="${this._resto.name}" loading="lazy" data-src="${CONFIG.BASE_IMAGE_URL_LARGE}${this._resto.pictureId}" class="lazyload">
            </picture>
            <div class="resto_city-rating">
              <p tabindex="0" aria-label="Lokasi ${this._resto.city}">${this._resto.city}</p>
              <p tabindex="0" aria-label="Rating ${this._resto.rating}"> ⭐${this._resto.rating}</span></p>
            </div>
          </div>
          <div class="resto_info">
            <h2 class="resto_name"><a href="/#/detail/${this._resto.id}" aria-label="Detail restoran ${this._resto.name}">${this._resto.name}</a></h2>
            <p tabindex="0" class="resto_desc">${this._resto.description}</p>
          </div>
        </article>
        `;
  }
}

customElements.define('resto-item', RestoItem);
