class RestoDetailMenu extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
    this._restoMenu = {
      foods: [],
      drinks: [],
    };
  }

  setMenus(value) {
    this._restoMenu.foods = this._setMenu('food', value.foods);
    this._restoMenu.drinks = this._setMenu('drink', value.drinks);
    this.render();
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      .menu_container {
        padding: 1em 2em;
        border: 0.1em solid var(--primaryColor);
        border-radius: 0.8em;
        margin-bottom: 1.5em;
        box-shadow: 0 0.1em 0.2em var(--primaryColor),
          0 0 0.2em var(--primaryColor) inset;
        .menu {
          h2,
          h3 {
            margin-top: 0.5em;
          }

          .menu_item {
            li {
              list-style-type: none;
            }
          }
        }
      }

      @media screen and (min-width: 600px) {
        
      }
    `;
  }

  _setMenu(category, menus) {
    const icon =
      category === 'food'
        ? '<icon-food></icon-food>'
        : '<icon-drink></icon-drink>';
    return menus
      ?.map(({ name }) => `<li tabindex="0">${icon}${name}</li>`)
      .join('');
  }

  render() {
    this.updateStyle();
    this.innerHTML = `
      ${this._style.outerHTML}
      <div class="menu_container">
        <h2 tabindex="0">Daftar Menu</h2>
        <div class="menu">
          <div class="menu_foods">
            <h3 tabindex="0">Menu Makanan</h3>
            <div class="menu_item">
              ${this._restoMenu.foods}
            </div>
          </div>

          <div class="menu_drinks">
            <h3 tabindex="0">Menu Minuman</h3>
            <div class="menu_item">
              ${this._restoMenu.drinks}
            </div>
          </div>
        </div>
      </div>
    `;
  }
}

customElements.define('resto-detail-menu', RestoDetailMenu);
