class RestoDetailLike extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
    `;
  }

  render() {
    this.updateStyle();
    this.innerHTML = `
      ${this._style.outerHTML}
      <button aria-label="like this restaurant" id="likeButton" class="like">
        <icon-like></icon-like>
      </button>
    `;
  }
}

customElements.define('resto-detail-like', RestoDetailLike);
