class RestoDetailReview extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
    this._restoReview = [];
  }

  setReviews(value) {
    this._restoReview = this._setReview(value);
    this.render();
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      .review {
        padding: 1em 2em;
        border: 0.1em solid var(--primaryColor);
        border-radius: 0.8em;
        margin-bottom: 1.5em;
        box-shadow: 0 0.1em 0.2em var(--primaryColor),
          0 0 0.2em var(--primaryColor) inset;

        .review_item {
          padding: 1em;
          border-radius: 1em;
          border: 0.1em solid var(--secondColor);
          margin-top: 0.9em;

          .review_item_user {
            border-bottom: 0.1em solid var(--black);
            padding-bottom: 0.2em;
            margin-bottom: 0.5em;
            icon-user {
              display: flex;
              align-items: center;
              flex-direction: row-reverse;
              justify-self: flex-start;
              gap: 0.4em;

              svg {
                font-size: 2.5rem;
                color: var(--primaryColor);
              }
            }
            .date {
              margin-left: 2.5em;
            }
          }
        }
      }
    `;
  }

  _setReview(reviews) {
    return reviews
      .map(
        ({ name, date, review }) => `
          <div class="review_item">
            <div class="review_item_user">
              <icon-user><p tabindex="0" aria-label="ulasan dari ${name}, pada ${date}, isi ulasan ${review}">${name}</p></icon-user>
              <p class="date" >${date}</p>
            </div>
            <p class="review_desc" >${review}</p>
          </div>
        `
      )
      .join('');
  }

  render() {
    this.updateStyle();
    this.innerHTML = `
      ${this._style.outerHTML}
      <div class="review">
        <h2 tabindex="0">Ulasan Restoran</h2>
        <div>
          ${this._restoReview}
        </div>
      </div>
    `;
  }
}

customElements.define('resto-detail-review', RestoDetailReview);
