class ReviewForm extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      .form-add-review {
        padding: 1em 2em;
        border: 0.1em solid var(--primaryColor);
        border-radius: 0.8em;
        margin-bottom: 1.5em;
        box-shadow: 0 0.1em 0.2em var(--primaryColor),
          0 0 0.2em var(--primaryColor) inset;

        form {
          display: flex;
          flex-direction: column;
          justify-content: center;
          .input {
            display: flex;
            flex-direction: column;

            label {
              margin-bottom: 0.5rem;
            }

            input,
            textarea {
              padding: 0.7rem;
              font-size: 1.1rem;
              outline: none;
              border: 0.1rem solid var(--primaryColor);
              margin-bottom: 0.5rem;
              color: var(--secondColor);

              border-radius: 0.5rem;

              &:focus {
                border: none;
                outline: 0.2rem solid var(--secondColor);
              }
            }

            textarea {
              height: 5rem;
            }
          }

          button {
            padding: 0.8rem 0;
            font-size: 1.25rem;
            margin: 0.5em 0;
            background-color : var(--white);

            border: 0.1rem solid var(--primaryColor);
            color: var(--secondColor);
            font-weight: 400;
            box-shadow: 0.1em 0.1em 0.1em var(--primaryColor);
            outline: none;
            border-radius: 0.5rem;
            transition: 0.3s;

            &:focus, &:hover, &:active {
              background-color : var(--primaryColor);
              color : var(--white);
              
            }
          }
        }
      }
    `;
  }

  render() {
    this.updateStyle();
    this.innerHTML = `
      ${this._style.outerHTML}
      <section class="form-add-review" id="form-add">
        <h2 tabindex="0">Tulis Ulasan Anda</h2>
        <form id="formReview">
          <div class="input">
            <label for="inputName">Nama</label>
            <input id="inputName" type="text"/>
          </div>
          <div class="input">
            <label for="inputReview">Ulasan Kamu</label>
            <textarea name="inputReview" id="inputReview"></textarea>
          </div>

          <button id="reviewSubmit" type="submit">Kirim Ulasan</button>
        </form>
      </section>
    `;
  }
}

customElements.define('review-form', ReviewForm);
