class RestoList extends HTMLElement {
  constructor() {
    super();

    this._style = document.createElement('style');
    this._restoList = [];
  }

  setRestoList(value) {
    this._restoList = value;

    this.render();
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      resto-list {
        margin: 0 auto;
        margin-bottom: 3em;

        width: 84vw;
        display: grid;
        gap: 3em;

        grid-template-columns: repeat(1, 1fr);
      }
    `;
  }

  render() {
    this.updateStyle();
    const restoItemElements = this._restoList.map((item) => {
      const resto = document.createElement('resto-item');
      resto.setResto(item);

      return resto;
    });

    this.innerHTML = '';
    this.append(this._style, ...restoItemElements);
  }
}

customElements.define('resto-list', RestoList);
